/*=================== fonts ====================*/
/* Medium Layout: 1280px */
/* Tablet Layout: 768px */
/* Mobile Layout: 320px */
/* Wide Mobile Layout: 480px */
/* =================================== */
/*  Basic Style 
/* =================================== */
body {
  color: #777777;
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.667; }

/* =================================== */
/*  Typography 
/* =================================== */
ul {
  list-style-type: none;
  margin: 0;
  padding: 0; }

a:hover {
  text-decoration: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Playfair Display", serif;
  color: #222222;
  font-weight: 700; }

h1,
.h1 {
  font-size: 70px;
  font-weight: 700; }

h2,
.h2 {
  font-size: 32px;
  font-weight: 700; }
  @media (min-width: 600px) {
    h2,
    .h2 {
      font-size: 42px; } }

a {
  display: inline-block; }

button {
  cursor: pointer; }

